@import "../../globalVariables.scss";
.wrapper {
  position: relative;
  width: 90%;
  height: 90%;
  background-color: $card-color;
  justify-self: center;
  border-radius: 10px;
  & > Button:first-of-type {
    position: absolute;
    right: 3px;
    top: 3px;
  }
  & > Button:nth-of-type(2) {
    position: absolute;
    right: 3px;
    bottom: 3px;
  }
  &:hover > .categoryMark {
    background-color: rgb(255, 0, 0);
  }
  & h3 {
    padding-left: 10%;
    background-color: #fff;
  }
}
.rowWrapper {
  display: flex;
  align-items: center;
  margin-bottom: 10%;

  .amountWrapper {
    margin: 0 10% 0 5%;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
  }
}

.categoryMark {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 4px;
  left: -4px;
  bottom: -4px;
  background-color: #fff;
  border-radius: 10px;
  z-index: -1;
}

@media (max-width: 768px) {
  .wrapper {
    width: 95%;
  }
}
