.wrapper {
  position: fixed;
  left: 20%;
  // transform: translateX(50%);
  width: 60%;
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
}

.amountLimit {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.radioWrapper {
  margin-bottom: 10vh;
  width: 100%;
  display: flex;
  justify-content: space-around;
}

@media (max-width: 768px) {
  .wrapper {
    width: 90%;
    left: 5%;
  }
}
