.wrapper {
  display: grid;
  grid-template-columns: 5% 1fr 5%;
  justify-items: center;
  align-items: center;
  padding: 30px 40px;
  background-color: rgb(115, 130, 168);
  box-shadow: 0 5px 15px 2px rgba(0, 0, 0, 0.5);
}

.navItemList {
  & img {
    width: 20px;
    height: 20px;
  }
  padding: 10px 15px;
  &Active {
    border: 2px solid #eee;
    border-radius: 10px;
  }
}

@media (max-width: 768px) {
  .wrapper {
    margin: 0;
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
