@import "../../GlobalVariables.scss";
.wrapper {
  display: flex;
  width: 500px;
  justify-content: space-around;
  align-items: center;
  margin: 20px 0;
  padding: 0;
}

.navItem {
  list-style: none;
  margin: 10px 20px;

  &List {
    text-decoration: none;
    color: #eee;
    padding: 10px 20px;
    &Active {
      border: 2px solid #eee;
      border-radius: 10px;
    }
  }
}

@media (max-width: 768px) {
  .wrapper {
    position: fixed;
    bottom: 0;
    right: 0;
    height: 10vh;
    width: 100%;
    margin: 0;
    z-index: 1;
    justify-content: space-around;
    background-color: rgb(115, 130, 168);
  }
}
