.radio {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  // margin: 20px;

  input {
    opacity: 0;
    position: absolute;
  }

  &Button {
    position: absolute;
    margin: 0;
    padding: 5px;
    width: 70px;
    height: 30px;
    border: 2px solid #1e58ff;
    border-radius: 20px;
    z-index: -1;
  }

  input:checked + &Button {
    background-color: #1e58ff;
  }
}

@media (max-width: 768px) {
  .radioButton {
    width: 60px;
    padding: 2px;
  }
}
